*{
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}

form{
  max-width: 1200px;
  padding: 1rem;
  /* margin: auto; */
}
form input{
  width: 50%;
  font-size: 30px;
  font-weight: bolder;
  background-color: black;
  color: blueviolet;
  border: none;
}
form  button{
  width: 20%;
  font-size: 30px;
  font-weight: bolder;
  background-color: blueviolet;
  color: white;
  border: none;
  cursor: pointer;
}
.list{
  width: 800px;
  /* margin: auto; */
  list-style: none;
  padding: 0.5rem;
  border-radius: 10px;
  box-shadow: 0 0 8px blue;
  cursor: pointer;
  color: black;
}

.list li {
  width: 100%;
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  box-shadow:  0 0 5px black;
  padding: 1rem;
  border-radius: 8px;
}
